// use server'
'use server';

/**
 * @fileOverview Categoriza automticamente los gastos basndose en una descripcin proporcionada por el usuario.
 *
 * - autoCategorizeExpense - Una funcin que maneja el proceso de categorizacin de gastos.
 * - AutoCategorizeExpenseInput - El tipo de entrada para la funcin autoCategorizeExpense.
 * - AutoCategorizeExpenseOutput - El tipo de retorno para la funcin autoCategorizeExpense.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const AutoCategorizeExpenseInputSchema = z.object({
  description: z
    .string()
    .describe('La descripcin del gasto a categorizar.'),
});
export type AutoCategorizeExpenseInput = z.infer<typeof AutoCategorizeExpenseInputSchema>;

const AutoCategorizeExpenseOutputSchema = z.object({
  category: z
    .string()
    .describe(
      'La categora predicha del gasto (e.g., Food, Transport, Rent, Utilities, Entertainment).' // Categories from the original prompt.
    ),
  confidence: z
    .number()
    .describe(
      'Un puntaje de confianza (0 a 1) que indica la certeza de la prediccin de la categora.'
    ),
});
export type AutoCategorizeExpenseOutput = z.infer<typeof AutoCategorizeExpenseOutputSchema>;

export async function autoCategorizeExpense(
  input: AutoCategorizeExpenseInput
): Promise<AutoCategorizeExpenseOutput> {
  return autoCategorizeExpenseFlow(input);
}

const prompt = ai.definePrompt({
  name: 'autoCategorizeExpensePrompt',
  input: {schema: AutoCategorizeExpenseInputSchema},
  output: {schema: AutoCategorizeExpenseOutputSchema},
  prompt: `Eres un asistente experto en finanzas personales. Tu trabajo es categorizar los gastos basndose en la descripcin proporcionada por el usuario.

  Dada la siguiente descripcin, determina la categora de gasto ms probable y un puntaje de confianza.

  Descripcin: {{{description}}}

  Categoras para elegir: Food, Transport, Rent, Utilities, Entertainment

  Asegrate de que el campo de categora contenga SOLAMENTE un valor de la lista de categoras aceptables. Emite la categora y un puntaje de confianza entre 0 y 1.
  `,
});

const autoCategorizeExpenseFlow = ai.defineFlow(
  {
    name: 'autoCategorizeExpenseFlow',
    inputSchema: AutoCategorizeExpenseInputSchema,
    outputSchema: AutoCategorizeExpenseOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
